body {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;

  color: #f2f2f2;
  font-size: 24px;

  background-color: #434343;
}
.background-Wrapper{
    position: fixed;
    top: 0;
    width: 100%;
    z-index: -1;
}

.background-BlackLine{
    margin: 0 auto;
    max-width: 1200px;
    height: 100vh;
    background-color: #2d2d2d;
    box-shadow: 0px 3px 13px 0px rgba(0, 0, 0, 0.29);
    opacity: 0.88;
    z-index: 4;
}

.background-Image{
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    z-index: -1;
}


.App {
  width: 100%;
  text-align: center;
  overflow: hidden;
}

.section-title {
  margin: 60px auto;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;   
  font-size: 50px;
  text-align: center;
  color: #f2f2f2;
}

@media (max-width: 1028px) {
  .section-title {
    margin: 60px auto;
    font-weight: 700;   
    font-size: 50px;
    color: #f2f2f2;
    letter-spacing: 2px;
  }
}

@media (max-width: 840px) {
    
}

@media (max-width: 600px) {
  .section-title {
    margin: 60px 40px;
    font-weight: 700;   
    font-size: 40px;
  }
}
header {
    position: fixed;
    width: 100%;
    height: 70px;
    background-color: #212121;
    z-index: 10;
}

@media (max-width: 640px) {
    .headMenu{
        margin: 0 auto;
        justify-content: center;
        flex-wrap: wrap-reverse;
        width: 80%;
        height: 100%;
    }

    .site-icon {
        display: none;
    }

    .menu-item {
        margin: 0;
    }
}

.header-inside {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    height: 100%;
    max-width: 1200px;
}

.headMenu{
    display: flex;
    align-items: center;
}

.menu-item {
    margin: 0 10px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    text-decoration: none;
    text-transform: uppercase;
    color: #eaeaea;
    transition: 0.4s;
    cursor: pointer;
}

.menu-item:hover {
    color: #ff841f;
}

.site-icon {
    margin-left: 30px;
    height: 41px;
}
.LanguagePanel {
    display: flex;
    margin: 0 13px;
    padding: 0;
    list-style: none;
    border-left: solid 2px #bebebe;
    border-right: solid 2px #bebebe;
}

.LanguagePanelItem {
    margin: 0 3px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    color: #eaeaea;
    transition: 0.4s;
    cursor: pointer;
}   

@media (max-width: 640px) {
    .LanguagePanelItem {
        font-size: 20px;
    }
}

.LanguagePanelItem:hover {
    color: #ffd45f;
}
.landing-block {
    position: relative;
    margin: 0 auto;
    max-width: 1200px;
    height: 100vh;
    min-height: 550px;
    overflow: hidden;
}

.centralTitle {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 28% auto;
    padding: 40px;
    padding-top: 50px;
    padding-left: 60px;
    width: 900px;
    height: 274px;
    box-sizing: border-box;
    border: solid 1px rgb(226, 226, 226);
    transition: 0.5s;
    cursor: pointer;
}

.centralTitle:hover {
    border: solid 1px rgb(255, 222, 112);
}

.centralTitle:hover .landing-underTitle{
    color: rgb(255, 222, 112);
}

.landing-title {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
    font-size: 73px;
    letter-spacing: 2px;
    text-align: left;
    color: #eeeeee;
}

.landing-underTitle {
    margin: 0;
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    font-size: 28px;
    letter-spacing: 2px;
    text-align: right;
    color: #eeeeee;
    transition: 0.5s;
}

.MoreButton {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    bottom: 14px;
    left: 50%;
    width: 56px;
    height: 70px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    cursor: pointer;
}

.MoreButton-text{
    margin: 0;
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    font-size: 30px;
    text-transform: lowercase;
    color: #d2d2d2;
}

.MoreButton-img {
    position: relative;
    width: 40px;
    -webkit-animation: arrow  1s infinite ease-in-out;
            animation: arrow  1s infinite ease-in-out;
}

@-webkit-keyframes arrow {
    from { 
        top: 0;
    }
    50% {
        top: 8px;
    }
    to {
        top: 0;
    }
}

@keyframes arrow {
    from { 
        top: 0;
    }
    50% {
        top: 8px;
    }
    to {
        top: 0;
    }
}

@media (max-width: 1028px) {
    .centralTitle {
        margin: 38vh auto;
        padding: 30px;
        padding-top: 50px;
        padding-left: 60px;
        width: 700px;
        height: 250px;
    }

    .landing-title {
        font-weight: 800;
        font-size: 58px;
 
    }

    .landing-underTitle {
        font-weight: 400;
        font-size: 20px;
        letter-spacing: 2px; 
    }
}

@media (max-width: 840px) {
    .centralTitle {
        margin: 38% auto;
        padding: 20px;
        padding-top: 44px;
        padding-left: 40px;
        width: 500px;
        height: 200px;
    }

    .landing-title {
        font-weight: 800;
        font-size: 40px;
 
    }

    .landing-underTitle {
        font-weight: 400;
        font-size: 14px;
        letter-spacing: 2px; 
    }
}

@media (max-width: 600px) {
    .centralTitle {
        margin: 40vh auto;
        padding: 14px;
        padding-top: 30px;
        padding-left: 26px;
        width: 300px;
        height: 150px;
    }

    .landing-title {
        font-weight: 800;
        font-size: 28px;
 
    }

    .landing-underTitle {
        font-weight: 400;
        font-size: 10px;
        letter-spacing: 2px; 
    }
}
.dailyPhotographer-block {
