}

.worksListItem {
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 10fr;
}

.search-page {
  margin: 0 auto;
  padding: 10px;

  text-align: center;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
}
